import {Video} from './Video'
import {PlayerState} from '@/models/Constants'

export class Player {

  player = null
  interval = null;
  endSeconds = null;
  startVideoId = 'E5i4rDc_GA8'


  constructor(
    public  YT: any,
    public id: number,
    public  elementDom: any,
    public eventHandler: any,
    public config: any,
  ) {
    const resultConfig = {...this.getPlayerConfig(), ...config}
    this.player = new YT.Player(elementDom, resultConfig);
  }

  play(video: Video){
    // console.log(' this.startVideoId === video.videoId', this.startVideoId === video.videoId)
    console.log('this.player.playVideo()')
    this.player.playVideo()
  }

  seek(video: Video){
    if (video.end) this.endSeconds = video.end
    this.player.seekTo(video.start);
    this.runCheckInterval()
  }

  play2(video: Video) {
    // console.log('start', video.start)
    if (video) {
      const playerState = this.player.getPlayerState();
      console.log('play')
      this.logState(playerState)
      switch (playerState) {
        case PlayerState.UNSTARTED:
          this.player.playVideo()
        break;
        case PlayerState.CUED:
          if (video.end) this.endSeconds = video.end
          // this.player.loadVideoById(video.videoId);


          //console.log('this.player.playVideo()')
          // if (video.start > 0) {
          setTimeout(()=>{
            this.player.playVideo();
            // console.log('player.playVideo')
            // this.player.seekTo(100);
            // console.log('player.seekTo', 100)
          },1000)


          // }
          break;
        case PlayerState.PLAYING:
          if (video.end) this.endSeconds = video.end
          this.player.seekTo(video.start);
          this.runCheckInterval()
          break;
        default:
          if (video.end) this.endSeconds = video.end
          this.player.seekTo(video.start);
          this.runCheckInterval()
      }

    }
  }

  private getPlayerConfig() {
    const Lscreen = window.innerWidth
    const largeur = Lscreen - 27; // add this line

    return {
      videoId: this.startVideoId,
      width: largeur,
      height: largeur * .78,

      host: 'https://www.youtube.com',
      playerVars: {
        autoplay: true,
        controls: 1, // Show pause/play  buttons in player
        showinfo: 0, // Hide the video title
        modestbranding: 1, // Hide the Youtube Logo
        // fs: 1, // Hide the full screen button
        // cc_load_policy: 0, // Hide closed captions
        iv_load_policy: 3, // Hide the Video Annotations
        autohide: 1 // Hide video controls when playing
      },
      events: {
        onStateChange: this.onStateChange,
        onError: this.onError,
        onReady: this.onReady,
      },
    };
  };

  onReady = (event) => {
    // this.play();
    this.eventHandler({id: this.id, state: PlayerState.READY})
    console.log('onReady')
  };

  onError = (error) => {
    console.error(error);
    this.eventHandler({id: this.id, state: error})
  };

  onStateChange = (event) => {
    this.eventHandler({id: this.id, state: event.data})
    // this.logState(event.data)
    // switch (event.data) {
    //   case PlayerState.ENDED:
    //     // this.play()
    //     break;
    //   case PlayerState.PAUSED:
    //     this.clearCheckInterval()
    //     break;
    //   case PlayerState.PLAYING:
    //     this.runCheckInterval()
    //     break
    //   default:
    //     console.log('I do not know what to do?')
    // case PlayerState.UNSTARTED:
    //   if (this.autoPlay) {
    //     this.player.playVideo()
    //   }
    //   break
    // case PlayerState.CUED:
    //   if (this.autoPlay) {
    //     this.player.playVideo()
    //   }
    //   break
    // case PlayerState.ENDED:
    //   // if (this.autoPlay) {
    //     console.log('ENDED')
    //   this.play();
    //   // }
    // default:
    // }
    // console.log(`onStateChange: ${event.data}`);
  };

  clearCheckInterval() {
    clearInterval(this.interval)
    this.interval = null
  }

  runCheckInterval() {
    if (!this.interval) {
      this.interval = setInterval(this.checkEndSeconds, 250)
    }
  }

  checkEndSeconds() {
    if (Number.isInteger(this.endSeconds) && this.player.getCurrentTime() - 1 >= this.endSeconds) {
      console.log(this.player.getCurrentTime())
      this.endSeconds = null
      this.clearCheckInterval()
      // this.play()
    }
  }

  logState(playerState) {
    console.log(this.id, Object.keys(PlayerState).find(key => PlayerState[key] === playerState))
  }
}
