import {Video} from './Video'

export class VideoList {
  videos: Array<Video> = []
  autoPlay: boolean
  currentIndex = 0
  defaultVideoId = 'xv9vcwaHcas'

  first() {
    return this.videos.length > 0 ? this.videos[0] : null
  }

  next() {
    const result = this.videos.length > 0 ? this.videos[this.currentIndex] : null
    let newIndex = this.currentIndex + 1
    this.currentIndex = newIndex + 1 > this.videos.length ? 0:newIndex
    return result
  }

  firstVideoId() {
    return this.first() ? this.first().videoId : this.defaultVideoId
  }

  static fromString(val) {
    const videoList = new VideoList()
    const params = new URLSearchParams(val);
    const autoPlayNumber = parseInt(params.get('a'))
    videoList.autoPlay = autoPlayNumber ===1
    const videoString = params.get('v') || 'E5i4rDc_GA8.0.91~E5i4rDc_GA8.147.276'


    videoString.split('~')
      .map(function (item, index) {
        const id = index + 1
        const arr = item.split('.')
        let videoId = arr[0]
        if (!videoId) {
          if (index > 0) {
            videoId = videoList.videos[index - 1].videoId
          } else {
            console.error('First videoId is empty')
            throw 'First videoId is empty'
          }
        }
        const start = arr[1] ? parseInt(arr[1]) : 0
        const end = arr[2] ? parseInt(arr[2]) : null
        if (Number.isInteger(start) && Number.isInteger(end) && end <= start) {
          console.error('endSeconds must be more startSeconds')
          throw 'endSeconds must be more startSeconds'
        }
        videoList.videos.push(new Video(id, videoId, start, end))
      });

    console.log(videoList)
    return videoList
  }
}
